.about_container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 10%;
}
.about_me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary), transparent);
    display: grid;
    place-items: center;
}
.about_me-image{
    border-radius: 3rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}
.about_me-image:hover {
    transform: rotate(0deg);
    border-radius: 0rem;
}
.about_cards{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
}
.about_card{
    text-align: center;
    padding: 2rem;
    background: var(--color-bg-variant);
    border-radius: 1rem;
    border:  1px solid transparent ;
    transition:  var(--transition);
}
.about_card:hover{
    background: transparent;
    border-color: var(--color-primary);
}
.about__icon {
    color: var(--color-primary);
    font-size: 1.5rem;
    margin-bottom: 1rem;
}
.about__card h4 {
    font-size: 1rem;
}
.about__card small {
    font-size: 0.9rem;
    color: var(--color-light);
}
.about_content p {
    margin: 2rem 0.3rem 2.6rem;
    font-size: 1rem;
    color: var(--color-light);
}

@media screen and (max-width: 1024px){
    .about_container {
        grid-template-columns: 1fr;
        gap: 0;
    }

    .about_me {
        width: 50%;
        margin: 2rem auto 4rem; 
    }

    .about_content p {
        margin: 1rem 0 1.5rem;
    }
}
  
@media screen and (max-width: 600px){
    .about_me {
        width: 65%;
        margin: 0rem auto 3rem; 
    }

    .about_cards {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .about_content {
        text-align: center;
    }

    .about_content p {
        margin: 1.5rem 0;
    }
}