header {
    height: 100%;
    padding-top: 3rem;
    overflow: hidden;
    z-index: 0;
  }
  
.header_container {
    text-align: center;
    height: 100%;
    position: relative;
    font-size: 1.3rem;
}

.cta {
    display: flex;
    justify-content: center;
    gap: 1.2rem;
    margin-top: 2.5rem;
}

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 5rem;
    top: 1rem;
}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    border-left: 1px dashed var(--color-primary);
}


.scroll__down {
    position: absolute;
    right: 5rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}
  
@media screen and (max-width: 1024px){
    header {
        padding-top: 3rem;
    }

    section {
        margin-top: 6rem;
    }
}

@media screen and (max-width: 600px){
    .header__socials,
    .scroll__down {
        display: none;
    }
}